a {
  color: inherit;
  text-decoration: none;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

html {
  --blue: #00acdc;
}

body {
  background-color: #eee;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.nav {
  text-transform: uppercase;
}

.nav__list {
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav__link:hover {
  color: var(--blue);
}

.is-active {
  color: var(--blue);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
